// utils/pdf.js
export function base64ToFile(b64, filename = "document.pdf") {
  const clean = b64.startsWith("data:") ? b64.split(",")[1] : b64;
  const bin = atob(clean);
  const bytes = new Uint8Array(bin.length);
  for (let i = 0; i < bin.length; i++) bytes[i] = bin.charCodeAt(i);

  // ใช้ File แทน Blob เพื่อติดชื่อไฟล์
  return new File([bytes], safeFilename(filename), { type: "application/pdf" });
}

function safeFilename(name) {
  const cleaned = name.replace(/[\/\\?<>\*:|"']/g, "_").trim();
  return cleaned.length ? cleaned.slice(0, 180) : "file.pdf";
}
