"use client";

import ViewPatientInfo from "@/components/ViewPatientInfo";
import axiosApi from "@/config/axios-api";
import React, { useEffect, useState } from "react";
import Ripple from "@nuttawoot_donut/react-ripple";
import Link from "next/link";
import { ChevronLeft } from "lucide-react";
import { ErrorBanner } from "@/components/ErrorBanner";
import LoadingCenter from "@/components/loading";
import Forbidden from "@/components/Forbidden";
import { cryptoDecode } from "@/config/crypto.mjs";
import { base64ToFile } from "@/utils/pdf";

export default function page({ params }) {
    const { id } = React.use(params);
    const an = decodeURIComponent(id);
    const [ deCodeAn, setDeCodeAn ] = useState("");
    const [data, setData] = useState({});
    const [ pdfUrl, setPdfUrl ] = useState(null);
    const [ statusError, setStatusError ] = useState(false);
    const [ messageError, setMessageError ] = useState("");
    const [ loading, setLoading ] = useState(false);

    let token = localStorage.getItem("token");

    const ripple = new Ripple();

    useEffect(() => {

        setLoading(true);

        if(!an.startsWith("U2FsdGVk")) {
            setStatusError(true);
            setLoading(false);
            setMessageError({
                message: "เกิดข้อผิดพลาด 400",
                config: { "url": `/pdf/${an}` },
                response: {
                    status: 400,
                    data: {
                    message: "ระบบตรวจพบการกรอกข้อมูล URL ที่ไม่ถูกต้องตามที่ระบบกําหนดเงื่อนไขข้อมูล",
                    errors: { "AN": "ตรวจพบรูปแบบข้อมูลไม่ถูกต้อง" }
                    }
                }
            });
        } else {
            fetchPdfByAn();
        }

    }, [])

    const fetchPdfByAn = async () => {
        let deHash = await cryptoDecode(an);

        if(!deHash) {
            setLoading(false);
            setMessageError({
                message: "เกิดข้อผิดพลาด 400",
                config: { "url": `/pdf/${an}` },
                response: {
                    status: 400,
                    data: {
                    message: "ระบบตรวจพบว่าหมายเลข AN ที่ได้รับมาไม่ถูกต้อง",
                    errors: { "AN": "ตรวจข้อมูลไม่ถูกต้อง" }
                    }
                }
            });
            setStatusError(true);
            return;
        };

        setDeCodeAn(deHash);
        setStatusError(false);

        try {
            const res = await axiosApi.get(`/mraIpd/fetchPdf/${deHash}`, {
                headers: {
                    Authorization: `Bearer ${token}`
                }
            });
            setData(res?.data?.data?.form_ipds ?? {});
            const file = base64ToFile(res?.data?.data?.pdf_ipd_file);
            const objectUrl = URL.createObjectURL(file);
            setPdfUrl(objectUrl);
            return () => URL.revokeObjectURL(objectUrl); // กันเมมรั่ว
        } catch (error) {
            console.log(error);
            setStatusError(true);
            setMessageError(error);
        } finally {
            setLoading(false);
        }
    }

    if(!token) {
        return <Forbidden />
    }

    if(loading){
        return <LoadingCenter title="ระบบกำลังตรวจสอบหมายเลข AN ที่ได้รับมา" />
    }

    if(statusError){
        return <ErrorBanner err={messageError} onRetry={fetchPdfByAn} autoRetrySec={15} errorId={Math.random().toString(36).slice(2, 10)}  />
    }

    return (
        <>
            <Link
                href="/"
                draggable={false}
                prefetch={true} // Pre-fetch the page 
                className="text-white w-fit bg-cyan-500 rounded-xl px-4 py-1.5 font-semibold cursor-pointer flex justify-center items-center gap-0.5 hover:shadow mb-5"
                onMouseDown={(e) => ripple.create(e)}
            >
                <ChevronLeft size={20} />ย้อนกลับ
            </Link>
            {/* infomation patient */}
            <ViewPatientInfo data={data} an={deCodeAn} />
            {/* PDF */}
            <div className="w-full h-[calc(100vh-25vh)]">
                <iframe className="rounded-xl" src={pdfUrl} width="100%" height="100%" allow="fullscreen" allowFullScreen />
            </div>
        </>
    )
}
